import React, { createContext, useState } from 'react';
import './App.css';
import {SecondComponent} from './secondComponent';

export const cntContext = React.createContext();

function App() {
  const [cnt, setCnt] = useState(1);
  return (
    <div className="App">
      <header className="App-header">
         <h1>Understanding useState and Context </h1>
         <cntContext.Provider value={{cnt, setCnt}}>
           <SecondComponent/>
         </cntContext.Provider> 
      </header>
    </div>
  );
}

export default App;
