import React, { useContext } from 'react';
import {cntContext} from './App'

export const ThirdComponent =()=>{
    const {cnt, setCnt} = useContext(cntContext);
    return (
        <div>
            <p>
                <select onChange={(e)=>{setCnt(e.target.value)}} style={{padding:'10px'}}>
                    <option value="1">Select</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                </select>
            </p>
            <button style={{padding:'20px', color:'white', backgroundColor: cnt>200?'red':'green'}}
                onClick={()=>{setCnt(cnt *2)}}
            >{cnt}</button>
        </div>
    )
}